// -------------------------------------------------------------
// Global styles
// -------------------------------------------------------------

*, *:before, *:after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	background: $light-grey;
	font-size: 50%;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	@include bp(tablet) {
		font-size: 57.25%;
	}

	@include bp(desktop) {
		font-size: 62.5%;
	}
}

body {
	line-height: $baseline;
	font-size: $global-font-size;
	font-family: $global-font-family;
	font-weight: $global-weight-normal;
	overflow-x: hidden;

	&.mobile-menu-active {
		overflow: hidden;
		max-height: 100vh;
	}

    &.mobile-menu-active .offcanvas {
		opacity: 1;
		visibility: visible;
		transition: all 60m0s 0s $cubic
	}
}

.wrapper {
	position: relative;
	max-width: 1800px;
	margin: 0 auto;
	background: $light;
	box-shadow: 0 0 100px rgba($dark, .1);
	overflow: hidden;

	@include bp(giant) {
		margin: 5em auto;
	}
}

.offcanvas {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	background: $primary;
	display: flex;
	max-height: 100vh;
	z-index: 300;
	transition: all 600ms 0s $cubic;
	opacity: 0;
	visibility: hidden;
}

.container {
	width:100%;
	max-width:$global-width;
	margin:0 auto;

	&--wide {
		max-width:$global-width*1.2;
		margin:0 auto;
	}
}

.overlay {
	display: none;

	@include bp(tablet) {
		position: absolute;
		background: rgba($primary, .7);
		top: 0; right: 0; bottom: 0; left: 0;
		width: 100%; height: 100%;
		z-index: 0;
	}
}
